import Vue from "vue";
import VueRouter from "vue-router";
import List from "@/page/list.vue";
import Create from "@/page/create.vue";
import Read from "@/page/read.vue";
import Update from "@/page/update.vue";
import Delete from "@/page/delete.vue";
import Answer from "@/page/answer.vue";
Vue.use(VueRouter);

const root = "/happyhouse/qna";

export const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      redirect: root, // 초기 진입 페이지 설정
    },
    {
      path: root,
      name: "list",
      component: List,
    },
    {
      path: root + "/create",
      name: "create",
      component: Create,
    },
    {
      path: root + "/read",
      name: "read",
      component: Read,
    },
    {
      path: root + "/update",
      name: "update",
      component: Update,
    },
    {
      path: root + "/delete",
      name: "delete",
      component: Delete,
    },
    {
      path: root + "/answer",
      name: "answer",
      component: Answer,
    },
  ],
});
